



var you;
var yourScore = 0;
var opponent;
var opponentScore = 0;
var opponentChoiceText = "";
var playerName = "Spelare";
var opponentName = "Dator";


var choices = ["rock", "paper", "scissors"];
var winningScore = 5; // Ange det poängantal som krävs för att vinna
var highscores = []; // Array för highscore


window.onload = function () {
  for (let i = 0; i < 3; i++) {
    let choice = document.createElement("img");
    choice.id = choices[i];
    choice.src = choices[i] + ".png";
    choice.addEventListener("click", selectChoice);
    document.getElementById("choices").append(choice);
  }
};


function selectChoice() {
  you = this.id;
  document.getElementById("your-choice").src = you + ".png";


  opponent = choices[Math.floor(Math.random() * 3)];
  document.getElementById("opponent-choice").src = opponent + ".png";


  if (you == opponent) {
    // Oavgjort, inga poäng tilldelas
  } else {
    if (you == "rock") {
      if (opponent == "scissors") {
        yourScore += 1;
      } else if (opponent == "paper") {
        opponentScore += 1;
        opponentChoiceText = "påse";
        if (opponentScore === 1) {
          resetGame(); // Nollställ spelet när datorn får ett poäng
        }
      }
    } else if (you == "scissors") {
      if (opponent == "paper") {
        yourScore += 1;
      } else if (opponent == "rock") {
        opponentScore += 1;
        opponentChoiceText = "sten";
        if (opponentScore === 1) {
          resetGame(); // Nollställ spelet när datorn får ett poäng
        }
      }
    } else if (you == "paper") {
      if (opponent == "rock") {
        yourScore += 1;
      } else if (opponent == "scissors") {
        opponentScore += 1;
        opponentChoiceText = "sax";
        if (opponentScore === 1) {
          resetGame(); // Nollställ spelet när datorn får ett poäng
        }
      }
    }
  }


  document.getElementById("your-score").innerText = yourScore;
  document.getElementById("opponent-score").innerText = opponentScore;
  document.getElementById("opponent-choice-text").innerText = opponentChoiceText;


  if (opponentScore === winningScore) {
    winnerName = `Tyvärr, ${opponentName} vann spelet!`;
    document.getElementById("winner").innerText = "Vinnare: " + winnerName;
  } else if (yourScore === winningScore) {
    winnerName = `Grattis, ${playerName}! Du vann!`;
    document.getElementById("winner").innerText = "Vinnare: " + winnerName;


    // Lägg till highscore här
    highscores.push({ name: playerName, score: yourScore });


    // Sortera highscore-listan i fallande ordning baserat på poäng
    highscores.sort((a, b) => b.score - a.score);


    // Begränsa listan till de fem bästa highscores
    highscores.splice(5);


    // Uppdatera highscore-listan på webbsidan
    updateHighscoreList();
  }
}


function resetGame() {
  yourScore = 0;
  opponentScore = 0;
  opponentChoiceText = "";
  document.getElementById("your-score").innerText = yourScore;
  document.getElementById("opponent-score").innerText = opponentScore;
  document.getElementById("opponent-choice-text").innerText = opponentChoiceText;
  document.getElementById("your-choice").src = "";
  document.getElementById("opponent-choice").src = "";
}


function setPlayerName() {
  playerName = document.getElementById("name-input").value;
  var playerNameDisplay = document.getElementById("player-name-display");
  playerNameDisplay.innerText = "Spelare: " + playerName;
}


function updateHighscoreList() {
  var highscoreList = document.getElementById("highscore-list");


  // Rensa befintliga listelement
  highscoreList.innerHTML = "";


  // Loopa igenom highscore-listan och skapa listelement för varje spelare
  for (var i = 0; i < highscores.length; i++) {
    var listItem = document.createElement("li");
    listItem.innerText = `${highscores[i].name}: ${highscores[i].score}`;
    highscoreList.appendChild(listItem);
  }
}


